import { Component, ElementRef, EventEmitter, HostBinding, Input } from '@angular/core';
import { OnInit, Output, SimpleChanges, TemplateRef } from '@angular/core';
/**
 * Componente tab del componente
 *
 * @export
 * @class BcTabComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'bc-tab',
  templateUrl: './bc-tab.component.html',
})
export class BcTabComponent implements OnInit {
  @HostBinding('class')
  elementClass: string;

    /**
   * Deshabilita el tab 
   * @type {('true | false)}
   * @memberof BcTabComponent
   */
    @Input() disabledTab: boolean;

  /**
   * label del tab
   *
   * @memberof BcTabComponent
   */
  @Input() label;
  /**
   * Template del tab
   *
   * @type {TemplateRef<any>}
   * @memberof BcTabComponent
   */
  @Input() labelTemplate: TemplateRef<any>;

  /**
   * Muestra o oculta el tab
   *
   * @type {boolean}
   * @memberof BcTabComponent
   */
  @Input() show: boolean = true;

  @Input() icon: string;

  /**
   * Valor cada vez que cambia el tab.
   *
   * @type {EventEmitter<boolean>}
   * @memberof BcCheckboxComponent
   */
  @Output() changeState: EventEmitter<any> = new EventEmitter<any>();

  classTab: string;

  constructor(private el: ElementRef) {}

  ngOnInit() {
    this.classTab = this.classTab != null ? this.classTab : 'bc-tab-content';
    this.elementClass = this.classTab;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (!changes.firstChange) {
      this.changeState.emit(null);
    }
  }
}
